.kmk__images{
  display: grid;
  grid-template-columns: 1fr;
  justify-items: center;
  align-content: center;
}

.kmk__image{
  width: 300px;
  padding: 8px;
}

#kmk_1{
  grid-area: k1;
}

#kmk_2{
    grid-area: k2;
  }

  #kmk_3{
    grid-area: k3;
  }

  #kmk_4{
    grid-area: k4;
  }

  #kmk_5{
    grid-area: k5;
  }

  #kmk_6{
    grid-area: k6;
  }

  #kmk_7{
    grid-area: k7;
  }

  #kmk_8{
    grid-area: k8;
  }

@media only screen and (min-width: 768px) {
  .kmk__images{
    grid-template-columns: 1fr 1fr;
    grid-template-areas: 
    'k1 k3'
    'k2 k4'
    'k5 k6'
    'k7 k8';
  }
}